<template>
  <div id="parent">
    <form class="addStudentForm">
      <div>
        <label>Student's Name</label>
        <input
          type="text"
          placeholder="Type a student's name"
          required="required"
          v-model="inputName"
        />
      </div>
      <div>
        <label>Student ID</label>
        <input
          type="text"
          placeholder="Type a student's ID"
          required="required"
          v-model="inputId"
        />
      </div>
      <button v-if="add" @click.prevent="addStudent">Add</button>
      <button v-else @click.prevent="updateInfo">Update</button>
    </form>

    <div v-if="errors" key="errorsBox">
      <transition-group
        tag="div"
        enter-active-class="animate__animated animate__backInLeft" 
        leave-active-class="animate__animated animate__backOutRight"
        mode="out-in"
      >
        <span class="errorSpan" v-for="error in errors" :key="error">{{error}}</span>
      </transition-group>
    </div>

  </div>
</template>

<script>
import { allFunc } from "../main";

export default {

  name: "Add",

  data: function() {
    return {
      inputName: null,
      inputId: null,
      add: true,
      update: allFunc.updataStudent,
      index: null,
      errors: []
    };
  },

  created() {
    let localInfo = JSON.parse(localStorage.getItem("myAllStudents"));
    if (localInfo != null) {
      allFunc.studentList = localInfo;
    }

    if (this.update != null) {
      this.inputName = this.update.name;
      this.inputId = this.update.id;
      this.add = false;
    }
  },

  methods: {
    addStudent() {
      let subThis = this;
      subThis.errors = [];
      if(subThis.inputName && subThis.inputId){
        if(isNaN(subThis.inputId)){
          this.errors.push("ID field should be a number");
        }
        if(!isNaN(subThis.inputName)){
          this.errors.push("Name field fan't be a number");
        }
        if(!subThis.errors.length){
          allFunc.addStudent(subThis.inputName, subThis.inputId);
          subThis.clearInput();
        }
      }else{
        subThis.errors = [];
        this.errors.push("You can't leave these fields empty, please type the data !");
      }
    },
    updateInfo() {
      allFunc.finalUpdate(this.inputName, this.inputId);
      this.add = true;
      allFunc.updataStudent = null;
      this.clearInput();
    },
    clearInput() {
      this.inputId = null;
      this.inputName = null;
    }
  }
};
</script>

<style lang="scss" scoped>
#parent{
  form {
    background-color: #f3f3f3;
    padding: 25px;
    margin: 50px 0;
    width: 100%;
    border-radius: 5px;
    position: relative;
    min-height: 285px;
    div {
      margin: 25px 0;
      label {
        display: block;
        margin: 8px 0;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 15px;
      }
      input {
        padding: 7px 10px;
        width: 100%;
        outline: none;
      }
    }
    button {
      position: absolute;
      right: 25px;
      bottom: 25px;
      background-color: #607d8b;
      color: #fff;
      padding: 8px 40px;
      border: none;
      outline: none;
      cursor: pointer;
      border-radius: 5px;
    }
  }
  div{
    span{
      width: 100%;
      display: block;
      background-color: #ffeaf1;
      color: #E91E63;
      padding: 20px;
      text-align: center;
    }
  }
}
@media (max-width:500px){
  #parent{
  form {
    padding: 15px;
    margin: 40px 0;
    border-radius: 3px;
    min-height: 210px;
    div {
      margin: 20px 0;
      label {
        margin: 5px 0;
        font-size: 12px;
      }
      input {
        padding: 6px 8px;
        font-size: 12px;
      }
    }
    button {
      right: 15px;
      bottom: 15px;
      padding: 6px 25px;
      font-size: 15px;
    }
  }
  div{
    span{
      width: 100%;
      display: block;
      background-color: #ffeaf1;
      color: #E91E63;
      padding: 20px;
      text-align: center;
    }
  }
}
}
</style>
