import Vue from "vue";
import VueRouter from "vue-router";
import Add from "../views/Add.vue";
import Records from "../views/Records.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Add",
    component: Add
  },
  {
    path: "/records",
    name: "Records",
    component: Records
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
